import {Injectable} from "@angular/core";
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class PageService {  
    constructor(private http: Http){}    
    registration(){       
       return this.http.get("assets/data/user-list.json")
              
        .map(res => res.json());   
     }     
     }


